form[is='modal-dialog'] {
  z-index: 1;
  position: fixed;
  inset: 0;
  margin: auto;
  height: fit-content;
  width: fit-content;
  max-height: calc(100dvh - 16px);
  max-width: calc(100dvw - 16px);
  border: none;
  border-radius: 6px;
  background-color: #fff;
  padding: 0;
  opacity: 0;
  overflow: hidden;
  pointer-events: none;
  transform: translateY(-50px);
  transition:
    opacity 275ms ease-out,
    transform 275ms ease-out;
}

modal-dialog[open],
form[is='modal-dialog'][open] {
  opacity: 1;
  pointer-events: all;
  transform: translateY(0);
  transition-duration: 125ms;
}
