import { validateProject } from '../validation';
export class ProjectService {
    constructor(repo) {
        this.repo = repo;
    }
    save(project) {
        validateProject(project);
        return this.repo.save(project);
    }
    findAll() {
        return this.repo.findAll();
    }
    findById(id) {
        return this.repo.findById(id);
    }
    deleteById(id) {
        return this.repo.deleteById(id);
    }
}
