/* CSS Resets */

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

html,
body {
  height: 100%;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

/* STYLES */

:root {
  --theme-color: hsl(180, 96%, 35%);
  --theme-color-light: hsl(180, 96%, 45%);
  --theme-color-dark: hsl(179, 95%, 15%);

  --color-gray-100: hsl(225, 25%, 95%);
  --color-gray-200: hsl(225, 16%, 90%);
  --color-gray-300: hsl(225, 8%, 80%);
  --color-gray-400: hsl(225, 8%, 70%);
  --color-gray-500: hsl(225, 7%, 60%);
  --color-gray-600: hsl(225, 15%, 50%);
  --color-gray-700: hsl(225, 12%, 40%);
  --color-gray-900: hsl(225, 25%, 20%);
  --color-gray-1000: hsl(225, 15%, 15%);

  --color-success: hsl(151, 67%, 45%);
  --color-success-light: hsla(151, 75%, 46%, 0.4);
  --color-attention: hsl(46, 99%, 50%);
  --color-danger: hsl(0, 100%, 50%);
  --color-danger-light: hsl(0, 90%, 70%);

  --padding-sm: 8px;
  --padding-md: 16px;
  --padding-lg: 32px;

  --border-radius: 6px;

  --transition-in-delay: 125ms;
  --transition-out-delay: 275ms;
  --transition-delay: var(--transition-out-delay);

  --shadow-color: 0deg 0% 63%;
  --shadow-elevation-medium: 0.3px 0.5px 0.7px hsl(var(--shadow-color) / 0.36),
    0.8px 1.6px 2px -0.8px hsl(var(--shadow-color) / 0.36),
    2.1px 4.1px 5.2px -1.7px hsl(var(--shadow-color) / 0.36),
    5px 10px 12.6px -2.5px hsl(var(--shadow-color) / 0.36);
  --shadow-elevation-high: 0.3px 0.5px 0.7px hsl(var(--shadow-color) / 0.34),
    1.5px 2.9px 3.7px -0.4px hsl(var(--shadow-color) / 0.34),
    2.7px 5.4px 6.8px -0.7px hsl(var(--shadow-color) / 0.34),
    4.5px 8.9px 11.2px -1.1px hsl(var(--shadow-color) / 0.34),
    7.1px 14.3px 18px -1.4px hsl(var(--shadow-color) / 0.34),
    11.2px 22.3px 28.1px -1.8px hsl(var(--shadow-color) / 0.34),
    17px 33.9px 42.7px -2.1px hsl(var(--shadow-color) / 0.34),
    25px 50px 62.9px -2.5px hsl(var(--shadow-color) / 0.34);

  --mobile-sidebar-width: min(100dvw, 300px);
}

html {
  font-family: 'Roboto', sans-serif;
  color: var(--color-gray-900);
  overflow: hidden !important;
}

.app {
  --header-height: 100px;
  height: 100%;

  display: grid;
  grid-template-columns: min(350px, 100dvw) calc(100dvw - min(350px, 100dvw));
  grid-template-rows: var(--header-height) calc(100% - var(--header-height));
  grid-template-areas:
    'header header'
    'sidebar main';

  /* overflow: hidden !important; */

  @media (max-width: 950px) {
    grid-template-areas:
      'header header'
      'main main';
  }

  transition: transform var(--transition-out-delay) ease-out;
}

.mobile-sidebar-open .app {
  transition-duration: var(--transition-in-delay);
  transform: translateX(var(--mobile-sidebar-width));
}

.header {
  grid-area: header;
  display: grid;
  place-content: center;
  position: relative;
  padding-block: var(--padding-md);
  color: var(--color-gray-200);
  background: linear-gradient(
    to right,
    var(--theme-color-dark) 0%,
    var(--theme-color) 60%,
    var(--theme-color-light)
  );
  box-shadow: var(--shadow-elevation-medium);
}

.sidebar {
  grid-area: sidebar;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  padding: var(--padding-md) var(--padding-md) 0;
  height: 100%;

  fieldset {
    border: none;
  }

  .project-actions {
    display: flex;
    flex-direction: column;
    padding: var(--padding-sm) 0;

    gap: var(--padding-sm);
    list-style-type: none;
  }

  .footer {
    margin-block-start: auto;
    text-align: center;
    padding-block-end: var(--padding-md);
  }

  @media (max-width: 950px) {
    width: var(--mobile-sidebar-width);
    position: fixed;
    top: 0;
    left: calc(var(--mobile-sidebar-width) * -1);
    /* transform: translateX(var(--mobile-sidebar-width)); */
  }
}

.mobile-sidebar-open .sidebar {
  transition-duration: var(--transition-in-delay);
  /* transform: translateX(var(--mobile-sidebar-width)); */
}

.main {
  grid-area: main;
  height: 100%;
  padding: var(--padding-md);
  overflow-x: auto;
  display: flex;
  flex-flow: row nowrap;
  gap: var(--padding-md);

  scroll-snap-type: x mandatory;

  .project {
    scroll-snap-align: start;
    scroll-margin-inline-start: var(--padding-md);

    --project-header-height: 75px;

    display: inline-block;
    flex: 0 0 100%;
    background-color: var(--color-gray-200);
    border-radius: var(--border-radius);
    overflow: hidden;
    height: 100%;

    box-shadow: var(--shadow-elevation-medium);

    flex-basis: min(calc(950px - 350px - 2 * var(--padding-md)), 100%);

    /* @media (min-width: 950px) {
      flex-basis: calc(950px - 350px - 2 * var(--padding-md));
    } */

    .project-header {
      background: linear-gradient(
        to right,
        var(--theme-color-dark),
        var(--theme-color)
      );
      color: var(--color-gray-200);
      display: flex;
      justify-content: space-between;
      padding: var(--padding-md);
      height: var(--project-header-height);
    }

    .todo-sections {
      display: flex;
      flex-direction: column;
      position: relative;
      gap: 8px;
      /*
        The following height calc is meant to constrain
        this container in a way that excess child elements
        trigger a scrollbar, instead of increasing its height
        offscreen.
      */
      height: calc(
        100dvh - var(--header-height) - var(--padding-md) * 2 -
          var(--project-header-height)
      );
      overflow-y: auto;
      padding: var(--padding-md);

      section {
        display: flex;
        flex-direction: column;
        gap: 8px;
      }

      .todos {
        display: flex;
        flex-direction: column;
        gap: 8px;
      }

      .todo {
        display: flex;
        align-items: center;
        position: relative;
        gap: 8px;
        cursor: pointer;

        background-color: var(--color-gray-100);
        border-radius: var(--border-radius);
        padding: var(--padding-sm);

        transition: filter var(--transition-out-delay) ease-out;
        box-shadow: var(--shadow-elevation-medium);

        scroll-margin: var(--padding-sm);

        input[type='checkbox'] {
          opacity: 0;
          height: 20px;
          width: 20px;
          cursor: pointer;
          pointer-events: none;
        }

        input[type='checkbox']:checked ~ .check-icon.fa-circle-check {
          opacity: 1;
        }

        .check-icon {
          position: absolute;
          left: 8px;
          top: 50%;
          color: var(--color-success);
          pointer-events: none;
        }

        .check-icon.fa-circle-check {
          opacity: 0;
        }

        h4 {
          font-weight: 400;
          pointer-events: none;
        }

        input[type='checkbox']:checked ~ h4 {
          text-decoration: line-through;
          color: var(--color-gray-400);
        }

        .todo-actions {
          display: flex;
          margin-inline-start: auto;

          .icon-button {
            height: 30px;
            width: 30px;
          }
        }
      }

      .todo:hover {
        transition-duration: var(--transition-in-delay);
        filter: brightness(0.9);
      }

      .todo.blink {
        --blink-color-from: var(--color-gray-100);
        --blink-color-to: var(--color-success-light);
        animation: blink 2 400ms linear forwards;
        animation-delay: 750ms;
      }

      .empty-todos {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: var(--padding-sm);
        position: absolute;
        height: fit-content;
        width: fit-content;
        margin: auto;
        inset: 0;
        background-color: var(--color-gray-100);
        padding: var(--padding-md);
        border-radius: var(--border-radius);
        box-shadow: var(--shadow-elevation-medium);
      }
    }
  }
}

table.todo-view {
  th {
    vertical-align: top;
    text-align: end;
    padding-inline-end: var(--padding-sm);
    font-size: 1.2rem;
  }
  td {
    font-size: 1.1rem;
  }
}

button {
  color: inherit;
  cursor: pointer;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

.action-button {
  background-color: var(--theme-color);
  color: var(--color-gray-200);
  border: none;
  border-radius: var(--border-radius);
  padding: var(--padding-sm);
  width: 100%;
  transition:
    transform var(--transition-out-delay) ease-out,
    box-shadow var(--transition-out-delay) ease-out;
  box-shadow: var(--shadow-elevation-medium);
  will-change: transform;

  &:hover {
    transition-duration: var(--transition-in-delay);
    transform: translate(-3px, -2px) scale(1.01);
    box-shadow: var(--shadow-elevation-high);
  }
}

.icon-button {
  border: none;
  height: 44px;
  width: 44px;
  padding: 0;
  background-color: inherit;

  i {
    transition:
      color var(--transition-out-delay) ease-out,
      filter var(--transition-out-delay) ease-out;
  }

  &:hover i {
    filter: brightness(0.85);
    transition-delay: var(--transition-in-delay);
    color: var(--theme-color-light);
  }

  &:hover .close-icon {
    color: var(--color-danger);
  }

  &:active {
    transform: scale(0.85);
  }
}

.modal-header,
.modal-body,
.modal-actions {
  padding: var(--padding-md);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(
    to right,
    var(--theme-color-dark),
    var(--theme-color)
  );
  color: var(--color-gray-200);
}

.modal-body {
  width: min(calc(100dvw - var(--padding-sm) * 2), 400px);
  max-height: 70dvh;
  overflow-y: auto;
}

.modal-actions {
  display: flex;
  justify-content: center;
  gap: 8px;

  > :first-child {
    margin-inline-start: auto;
  }

  .status {
    display: flex;
    align-items: center;
    margin-inline: 0 auto;
    padding-inline-start: var(--padding-sm);

    .icon.success {
      color: var(--color-success);
    }

    .icon.attention {
      color: var(--color-attention);
    }

    .icon.danger {
      color: var(--color-danger-light);
    }
  }

  button {
    --background: var(--theme-color-dark);
    --color: var(--color-gray-100);

    color: var(--color);
    background-color: var(--background);
    border: 1px solid transparent;
    border-radius: var(--border-radius);
    padding: var(--padding-sm) var(--padding-md);

    transition:
      filter var(--transition-in-delay) ease-out,
      background-color var(--transition-out-delay) ease-out,
      color var(--transition-out-delay) ease-out;

    &.ghost {
      color: var(--background);
      background-color: transparent;
      border-color: var(--background);
    }

    &.attention {
      --background: var(--color-attention);
    }

    &.danger {
      --background: var(--color-danger);
    }

    &.success {
      --background: var(--color-success);
    }

    &:not(.ghost):hover {
      filter: brightness(0.9);
    }

    &.ghost:hover {
      background-color: var(--background);
      color: var(--color);
    }

    &:hover {
      transition-duration: var(--transition-in-delay);
    }
  }
}

.form-control {
  display: flex;
  flex-direction: column-reverse;

  padding-block: var(--padding-sm);

  &:first-of-type {
    padding-block-start: var(--padding-md);
  }

  &:last-of-type {
    padding-block-end: var(--padding-md);
  }

  label {
    color: var(--color-gray-500);
    transition: color var(--transition-in-delay) ease-in-out;
  }

  input,
  textarea,
  select {
    background-color: var(--color-gray-200);
    color: var(--color-gray-500);
    border: 2px solid var(--color-gray-300);
    border-radius: var(--border-radius);
    padding: var(--padding-sm);

    transition:
      color var(--transition-out-delay) ease-in-out,
      border-color var(--transition-out-delay) ease-in-out,
      outline-color var(--transition-out-delay) ease-in-out;

    &::placeholder {
      color: var(--color-gray-300);
    }

    &:focus-visible {
      outline-color: var(--theme-color);
    }
  }

  textarea {
    resize: none;
  }

  select {
    cursor: pointer;
  }

  input[type='datetime-local']::-webkit-calendar-picker-indicator {
    color-scheme: dark;
    cursor: pointer;
    background-color: var(--color-gray-300);
    padding: var(--padding-sm);
    border-radius: 50%;
    transition: background-color var(--transition-out-delay) ease-out;
  }

  input[type='datetime-local']:hover::-webkit-calendar-picker-indicator {
    background-color: var(--theme-color);
    transition-duration: var(--transition-in-delay);
  }

  input.invalid,
  textarea.invalid,
  select.invalid {
    border-color: var(--color-danger);
    color: var(--color-danger-light);
    transition-duration: var(--transition-in-delay);
  }

  input.invalid + label,
  textarea.invalid + label,
  select.invalid + label {
    color: var(--color-danger-light);
    transition-duration: var(--transition-in-delay);
  }
}

.text-overflow-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

i.icon {
  pointer-events: none;
}

ul.errors {
  list-style: none;
  padding: 0;
  color: var(--color-danger-light);

  & li {
    padding-block: var(--padding-sm);
  }

  & li::before {
    content: '* ';
  }
}

.sidebar-toggle-button {
  position: absolute;
  background-color: inherit;
  border: none;
  height: 44px;
  width: 44px;
  top: 0;
  bottom: 0;
  left: 8px;
  margin: auto;

  display: none;
  z-index: 2;

  @media (max-width: 950px) {
    display: block;
  }

  i.icon {
    position: absolute;
    left: 0;
    right: 0;
    transition: opacity var(--transition-out-delay) ease-in-out;
  }

  i.close {
    opacity: 0;
  }
}

.mobile-sidebar-open .sidebar-toggle-button {
  i.burguer {
    opacity: 0;
  }

  i.close {
    opacity: 1;
  }
}

@media (min-width: 500px) {
  ::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: var(--color-gray-300);
  }

  ::-webkit-scrollbar-thumb {
    border-radius: 1000px;
    background-color: var(--theme-color);
    border: 2px solid var(--color-gray-300);
  }

  ::-webkit-scrollbar-thumb:hover {
    background-color: var(--theme-color-light);
  }
}

@keyframes blink {
  0% {
    background-color: var(--blink-color-from);
  }
  35%,
  70% {
    background-color: var(--blink-color-to);
  }
  100% {
    background-color: var(--blink-color-from);
  }
}
