AsC;EACxC;;EAEA;IACE,oCAAoC;EACtC;;EAEA;IACE,4BAA4B;IAC5B,wDAAwD;EAC1D;;EAEA;;;IAGE,uCAAuC;IACvC,4BAA4B;IAC5B,uCAAuC;IACvC,mCAAmC;IACnC,0BAA0B;;IAE1B;;;2DAGuD;;IAEvD;MACE,4BAA4B;IAC9B;;IAEA;MACE,iCAAiC;IACnC;EACF;;EAEA;IACE,YAAY;EACd;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,kBAAkB;IAClB,eAAe;IACf,uCAAuC;IACvC,0BAA0B;IAC1B,kBAAkB;IAClB,iEAAiE;EACnE;;EAEA;IACE,oCAAoC;IACpC,+CAA+C;EACjD;;EAEA;;;IAGE,iCAAiC;IACjC,gCAAgC;IAChC,+CAA+C;EACjD;;EAEA;;;IAGE,gCAAgC;IAChC,+CAA+C;EACjD;AACF;;AAEA;EACE,gBAAgB;EAChB,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA;EACE,oBAAoB;AACtB;;AAEA;EACE,gBAAgB;EAChB,UAAU;EACV,gCAAgC;;EAEhC;IACE,gCAAgC;EAClC;;EAEA;IACE,aAAa;EACf;AACF;;AAEA;EACE,kBAAkB;EAClB,yBAAyB;EACzB,YAAY;EACZ,YAAY;EACZ,WAAW;EACX,MAAM;EACN,SAAS;EACT,SAAS;EACT,YAAY;;EAEZ,aAAa;EACb,UAAU;;EAEV;IACE,cAAc;EAChB;;EAEA;IACE,kBAAkB;IAClB,OAAO;IACP,QAAQ;IACR,2DAA2D;EAC7D;;EAEA;IACE,UAAU;EACZ;AACF;;AAEA;EACE;IACE,UAAU;EACZ;;EAEA;IACE,UAAU;EACZ;AACF;;AAEA;EACE;IACE,WAAW;IACX,YAAY;IACZ,uCAAuC;EACzC;;EAEA;IACE,qBAAqB;IACrB,oCAAoC;IACpC,uCAAuC;EACzC;;EAEA;IACE,0CAA0C;EAC5C;AACF;;AAEA;EACE;IACE,yCAAyC;EAC3C;EACA;;IAEE,uCAAuC;EACzC;EACA;IACE,yCAAyC;EAC3C;AACF","sourcesContent":["@charset \"utf-8\";\n\n/* CSS Resets */\n\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\n* {\n  margin: 0;\n}\n\nhtml,\nbody {\n  height: 100%;\n}\n\nbody {\n  line-height: 1.5;\n  -webkit-font-smoothing: antialiased;\n}\n\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n  display: block;\n  max-width: 100%;\n}\n\ninput,\nbutton,\ntextarea,\nselect {\n  font: inherit;\n}\n\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  overflow-wrap: break-word;\n}\n\n/* STYLES */\n\n:root {\n  --theme-color: hsl(180, 96%, 35%);\n  --theme-color-light: hsl(180, 96%, 45%);\n  --theme-color-dark: hsl(179, 95%, 15%);\n\n  --color-gray-100: hsl(225, 25%, 95%);\n  --color-gray-200: hsl(225, 16%, 90%);\n  --color-gray-300: hsl(225, 8%, 80%);\n  --color-gray-400: hsl(225, 8%, 70%);\n  --color-gray-500: hsl(225, 7%, 60%);\n  --color-gray-600: hsl(225, 15%, 50%);\n  --color-gray-700: hsl(225, 12%, 40%);\n  --color-gray-900: hsl(225, 25%, 20%);\n  --color-gray-1000: hsl(225, 15%, 15%);\n\n  --color-success: hsl(151, 67%, 45%);\n  --color-success-light: hsla(151, 75%, 46%, 0.4);\n  --color-attention: hsl(46, 99%, 50%);\n  --color-danger: hsl(0, 100%, 50%);\n  --color-danger-light: hsl(0, 90%, 70%);\n\n  --padding-sm: 8px;\n  --padding-md: 16px;\n  --padding-lg: 32px;\n\n  --border-radius: 6px;\n\n  --transition-in-delay: 125ms;\n  --transition-out-delay: 275ms;\n  --transition-delay: var(--transition-out-delay);\n\n  --shadow-color: 0deg 0% 63%;\n  --shadow-elevation-medium: 0.3px 0.5px 0.7px hsl(var(--shadow-color) / 0.36),\n    0.8px 1.6px 2px -0.8px hsl(var(--shadow-color) / 0.36),\n    2.1px 4.1px 5.2px -1.7px hsl(var(--shadow-color) / 0.36),\n    5px 10px 12.6px -2.5px hsl(var(--shadow-color) / 0.36);\n  --shadow-elevation-high: 0.3px 0.5px 0.7px hsl(var(--shadow-color) / 0.34),\n    1.5px 2.9px 3.7px -0.4px hsl(var(--shadow-color) / 0.34),\n    2.7px 5.4px 6.8px -0.7px hsl(var(--shadow-color) / 0.34),\n    4.5px 8.9px 11.2px -1.1px hsl(var(--shadow-color) / 0.34),\n    7.1px 14.3px 18px -1.4px hsl(var(--shadow-color) / 0.34),\n    11.2px 22.3px 28.1px -1.8px hsl(var(--shadow-color) / 0.34),\n    17px 33.9px 42.7px -2.1px hsl(var(--shadow-color) / 0.34),\n    25px 50px 62.9px -2.5px hsl(var(--shadow-color) / 0.34);\n\n  --mobile-sidebar-width: min(100dvw, 300px);\n}\n\nhtml {\n  font-family: 'Roboto', sans-serif;\n  color: var(--color-gray-900);\n  overflow: hidden !important;\n}\n\n.app {\n  --header-height: 100px;\n  height: 100%;\n\n  display: grid;\n  grid-template-columns: min(350px, 100dvw) calc(100dvw - min(350px, 100dvw));\n  grid-template-rows: var(--header-height) calc(100% - var(--header-height));\n  grid-template-areas:\n    'header header'\n    'sidebar main';\n\n  /* overflow: hidden !important; */\n\n  @media (max-width: 950px) {\n    grid-template-areas:\n      'header header'\n      'main main';\n  }\n\n  transition: transform var(--transition-out-delay) ease-out;\n}\n\n.mobile-sidebar-open .app {\n  transition-duration: var(--transition-in-delay);\n  transform: translateX(var(--mobile-sidebar-width));\n}\n\n.header {\n  grid-area: header;\n  display: grid;\n  place-content: center;\n  position: relative;\n  padding-block: var(--padding-md);\n  color: var(--color-gray-200);\n  background: linear-gradient(\n    to right,\n    var(--theme-color-dark) 0%,\n    var(--theme-color) 60%,\n    var(--theme-color-light)\n  );\n  box-shadow: var(--shadow-elevation-medium);\n}\n\n.sidebar {\n  grid-area: sidebar;\n  background-color: #fff;\n  display: flex;\n  flex-direction: column;\n  padding: var(--padding-md) var(--padding-md) 0;\n  height: 100%;\n\n  fieldset {\n    border: none;\n  }\n\n  .project-actions {\n    display: flex;\n    flex-direction: column;\n    padding: var(--padding-sm) 0;\n\n    gap: var(--padding-sm);\n    list-style-type: none;\n  }\n\n  .footer {\n    margin-block-start: auto;\n    text-align: center;\n    padding-block-end: var(--padding-md);\n  }\n\n  @media (max-width: 950px) {\n    width: var(--mobile-sidebar-width);\n    position: fixed;\n    top: 0;\n    left: calc(var(--mobile-sidebar-width) * -1);\n    /* transform: translateX(var(--mobile-sidebar-width)); */\n  }\n}\n\n.mobile-sidebar-open .sidebar {\n  transition-duration: var(--transition-in-delay);\n  /* transform: translateX(var(--mobile-sidebar-width)); */\n}\n\n.main {\n  grid-area: main;\n  height: 100%;\n  padding: var(--padding-md);\n  overflow-x: auto;\n  display: flex;\n  flex-flow: row nowrap;\n  gap: var(--padding-md);\n\n  scroll-snap-type: x mandatory;\n\n  .project {\n    scroll-snap-align: start;\n    scroll-margin-inline-start: var(--padding-md);\n\n    --project-header-height: 75px;\n\n    display: inline-block;\n    flex: 0 0 100%;\n    background-color: var(--color-gray-200);\n    border-radius: var(--border-radius);\n    overflow: hidden;\n    height: 100%;\n\n    box-shadow: var(--shadow-elevation-medium);\n\n    flex-basis: min(calc(950px - 350px - 2 * var(--padding-md)), 100%);\n\n    /* @media (min-width: 950px) {\n      flex-basis: calc(950px - 350px - 2 * var(--padding-md));\n    } */\n\n    .project-header {\n      background: linear-gradient(\n        to right,\n        var(--theme-color-dark),\n        var(--theme-color)\n      );\n      color: var(--color-gray-200);\n      display: flex;\n      justify-content: space-between;\n      padding: var(--padding-md);\n      height: var(--project-header-height);\n    }\n\n    .todo-sections {\n      display: flex;\n      flex-direction: column;\n      position: relative;\n      gap: 8px;\n      /*\n        The following height calc is meant to constrain\n        this container in a way that excess child elements\n        trigger a scrollbar, instead of increasing its height\n        offscreen.\n      */\n      height: calc(\n        100dvh - var(--header-height) - var(--padding-md) * 2 -\n          var(--project-header-height)\n      );\n      overflow-y: auto;\n      padding: var(--padding-md);\n\n      section {\n        display: flex;\n        flex-direction: column;\n        gap: 8px;\n      }\n\n      .todos {\n        display: flex;\n        flex-direction: column;\n        gap: 8px;\n      }\n\n      .todo {\n        display: flex;\n        align-items: center;\n        position: relative;\n        gap: 8px;\n        cursor: pointer;\n\n        background-color: var(--color-gray-100);\n        border-radius: var(--border-radius);\n        padding: var(--padding-sm);\n\n        transition: filter var(--transition-out-delay) ease-out;\n        box-shadow: var(--shadow-elevation-medium);\n\n        scroll-margin: var(--padding-sm);\n\n        input[type='checkbox'] {\n          opacity: 0;\n          height: 20px;\n          width: 20px;\n          cursor: pointer;\n          pointer-events: none;\n        }\n\n        input[type='checkbox']:checked ~ .check-icon.fa-circle-check